.show-all {
  position: relative;
  background-color: $white;

  @media (min-width: $tablet-width) {
    border: 2px solid $light-grey;
    padding-top: 108px;
  }
}

.show-all__title {
  margin: 0 0 8px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  font-family: $font-extra;
  text-transform: uppercase;
  color: $black-2;

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;
    max-width: 178px;
    font-size: 20px;
    line-height: 22px;
  }
}

.show-all__button {
  width: 100%;

  @media (max-width: $mobile-only) {
    max-width: 280px;
  }
}

.show-all__text {
  margin: 0 0 20px;

  @media (min-width: $tablet-width) {
    margin-bottom: 87px;
    max-width: 178px;
    font-size: 16px;
    line-height: 18px;
  }
}
