.program-list {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.program-list__item {
  background-color: $light-grey;
  padding: 20px;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    padding: 75px 275px 60px 50px;
    margin-bottom: 31px;
  }

  @media (min-width: $desktop-width) {
    width: 47%;
    padding: 75px 50px 60px 50px;
  }
}
